:root {
  --brown: #4d1d1d;
  --red: #a50202;
  --blue: #000150;
  --green: #4d7e51;
  --beige: #fff2d9;
}

* {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

*:focus {
  outline: none;
}

body {
  scroll-behavior: smooth;
  font-family: "Irish Grover", system-ui;
}

img {
  width: 100%;
  height: auto;
  line-height: 0;
  display: block;
}

a {
  text-decoration-line: none;
}

ul {
  list-style-type: none;
}

button {
  cursor: pointer;
  display: block;
  border: none;
  background-color: transparent;
}

.item {
  background-color: var(--brown);
  padding: 3px;
  width: 200px;
  border-radius: 100px;
  height: 279px;
}

.item__wrapper {
  background-color: var(--beige);
  padding: 0 16px;
  border-radius: 96px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-position: 11px -44px, 11px 235px;
  background-repeat: no-repeat, no-repeat;
  background-size: auto, auto;
}

.item:nth-child(3n - 2) .item__wrapper {
  background-image: url(./svg/oval-red.svg), url(./svg/oval-red.svg);
}

.item:nth-child(3n - 1) .item__wrapper {
  background-image: url(./svg/oval-blue.svg), url(./svg/oval-blue.svg);
}

.item:nth-child(3n) .item__wrapper {
  background-image: url(./svg/oval-green.svg), url(./svg/oval-green.svg);
}

.item__subtext {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4px;
}

.item__subtitle {
  text-transform: capitalize;
  color: var(--brown);
  font-size: 20px;
}

.item__price {
  font-size: 20px;
}

.item:nth-child(3n - 2) .item__price {
  color: var(--red);
}

.item:nth-child(3n - 1) .item__price {
  color: var(--blue);
}

.item:nth-child(3n) .item__price {
  color: var(--green);
}

.item__sublist {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.item__subitem {
}

.item__subdesc {
  color: var(--brown);
  font-size: 16px;
}

.item__subspan {
  text-transform: capitalize;
}

.item:nth-child(3n - 2) .item__subspan {
  color: var(--red);
}

.item:nth-child(3n - 1) .item__subspan {
  color: var(--blue);
}

.item:nth-child(3n) .item__subspan {
  color: var(--green);
}

.item__btn {
  color: var(--brown);
  font-size: 16px;
  margin-top: 16px;
  padding: 10px 0;
  width: 100%;
  border-radius: 350px;
  border: 3px solid var(--brown);
}

.item__btn:hover {
  color: var(--beige);
}

.item:nth-child(3n - 2) .item__btn:hover {
  background-color: var(--red);
}

.item:nth-child(3n - 1) .item__btn:hover {
  background-color: var(--blue);
}

.item:nth-child(3n) .item__btn:hover {
  background-color: var(--green);
}
